.fxgrid,
[class^="col:"] {
  box-sizing: border-box;
}

.fxgrid,
.row {
  --fxgrid-gap: 0.5rem;
  display: flex;
  flex: 0 1 auto;
  flex-wrap: wrap;
  flex-direction: row;
  margin-right: calc(var(--fxgrid-gap) * (-1));
  margin-left: calc(var(--fxgrid-gap) * (-1));
}

.col\:1\@mobile {
  padding: calc(var(--fxgrid-gap));
  flex-basis: 25%;
  max-width: 25%;
}

.col\:2\@mobile {
  padding: calc(var(--fxgrid-gap));
  flex-basis: 50%;
  max-width: 50%;
}

